<template>
  <div class="container login">
    <figure class="figure text-center d-flex justify-content-center">
      <img src="../assets/account-manager.png" class="figure-img img-fluid rounded" width="140" height="140">
    </figure>
    <form>
      <div class="input-group mb-3">
        <span class="input-group-text">
          <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-person-fill"
            viewBox="0 0 16 16">
            <path d="M3 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6" />
          </svg>
        </span>
        <div class="form-floating">
          <input type="email" v-model="user.email" class="form-control" id="floatingInput" placeholder="E-mail"
            required>
          <label for="floatingInput">E-mail</label>
        </div>
      </div>
      <div class="input-group mb-3">
        <span class="input-group-text">
          <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-lock-fill"
            viewBox="0 0 16 16">
            <path
              d="M8 1a2 2 0 0 1 2 2v4H6V3a2 2 0 0 1 2-2m3 6V3a3 3 0 0 0-6 0v4a2 2 0 0 0-2 2v5a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V9a2 2 0 0 0-2-2" />
          </svg>
        </span>
        <div class="form-floating">
          <input type="password" v-model="user.password" class="form-control" id="floatingPassword" placeholder="Senha"
            required>
          <label for="floatingPassword">Senha</label>
        </div>
      </div>
    </form>
    <span class="text-muted d-flex justify-content-center gap-3 mb-3">Entrar com</span>
    <div class="d-flex justify-content-center gap-3">
      <button class="btn btn-dark" @click="signInOAuth('github')">
        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-github"
          viewBox="0 0 16 16">
          <path
            d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27s1.36.09 2 .27c1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.01 8.01 0 0 0 16 8c0-4.42-3.58-8-8-8" />
        </svg>
      </button>
    </div>
  </div>
</template>

<script>
import { reactive } from 'vue';
import { signInWithPassword } from '@/services/auth/loginService';
import { signInWithOAuth } from '@/services/auth/loginService';

export default {
  name: 'LoginView',
  setup() {
    const user = reactive({
      email: '',
      password: ''
    });

    const signInPassword = async () => {
      if (user.email === '' || user.password === '') {
        return;
      }
      await signInWithPassword(user.email, user.password);
    };

    const signInOAuth = async (provider) => {
      await signInWithOAuth(provider);
    };

    return {
      user,
      signInPassword,
      signInOAuth
    };
  }
};
</script>

<style scoped>
.form-control {
  border-radius: 15px;
  height: 50px;
}

.bi-arrow-right {
  color: #41b883;
}

.login {
  max-width: 398px;
  background-color: #8881;
}
</style>
